<template>
  <div class="baseMulClassAnalyseBtn">
    <el-button type="primary" plain @click="visiableDialog = true">
      
    </el-button>
    <el-dialog
      title=""
      :visible.sync="visiableDialog"
      :close-on-click-modal="false"
      @close="onClose"
    >
      <el-form :model="formData" ref="form" :rules="rules" inline>
        <el-form-item label="" prop="name">
          <el-input v-model="formData.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="classes">
          <el-select
            v-model="formData.classes"
            multiple
            filterable
            collapse-tags
            placeholder=""
            value-key="id"
            clearable
            :multiple-limit="14"
            style="width: 300px"
          >
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.name"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <div>
          
          <span v-for="(item, index) in formData.classes" :key="item.id">
            {{ item.name }}{{ item.className || '' }}
            <span v-if="index !== formData.classes.length - 1"></span>
            <!-- <el-link :underline="false" icon="el-icon-d-arrow-left" @click=""></el-link> -->
          </span>
        </div>
      </el-form>
      <span slot="footer">
        <el-button type="primary" @click="onSave"></el-button>
      </span>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="tableDialog"
      v-if="tableDialog"
      width="80%"
      top="10vh"
    >
      <total-sort-template
        :classes="formData.classes.map(item => item.id)"
        :isDialog="true"
        @onAddTable="
          tableDialog = false
          visiableDialog = false
        "
      ></total-sort-template>
    </el-dialog>
  </div>
</template>

<script>
import TotalSortTemplate from '../template/totalSortTemplate'
import baseMixin from './baseMixin'
import { isRepeat } from '@/common/utils'
export default {
  name: 'BaseMulClassAnalyseBtn',
  mixins: [baseMixin],
  components: {
    TotalSortTemplate
  },
  computed: {
    tables() {
      return this.$store.state.tables
    },
    options() {
      return this.tables.filter(item => item.sortObj)
    }
  },
  data() {
    return {
      tableDialog: false,
      visiableDialog: false,
      formData: {
        name: '',
        classes: []
      },
      rules: {
        name: [{ required: true, message: '' }],
        classes: [{ required: true, message: '' }]
      }
    }
  },
  methods: {
    async onSave() {
      try {
        await this.$refs.form.validate()
        let res = this.formData.classes.find(item => !item.className)
        if (res) {
          this.$message.error(`${res.name}`)
          return false
        }
        if (isRepeat(this.formData.classes.map(item => item.className))) {
          this.$message.error('')
          return false
        }
        this.tableDialog = true
      } catch (err) {
        console.log(err)
      }
    },
    onClose() {
      this.formData.classes = []
    }
  }
}
</script>

<style lang="scss" scoped>
.baseMulClassAnalyseBtn {
  display: inline-block;
}
</style>
