<template>
  <div class="scoreTemplate">
    <div class="btns">
      <div class="left">
        <base-class-setting
          ref="setting"
          v-model="curTable.className"
        ></base-class-setting>
        <template v-if="curTable.sortObj">
          <el-tooltip class="item" effect="dark" placement="top-start">
            <div slot="content">
              / - 
              <br />
               > 50
              <br />
               &lt; -50
            </div>
            <el-button
              type="success"
              plain
              @click="rankCompareDialog.show = true"
            >
              
            </el-button>
          </el-tooltip>
          <el-button
            plain
            type="primary"
            size="small"
            class="btn"
            @click="openSortDialog"
          >
            
          </el-button>
        </template>
      </div>
      <div class="right">
        <el-radio-group v-model="radio" @change="typeChangeHandle">
          <el-radio-button :label="1"></el-radio-button>
          <el-radio-button :label="2"></el-radio-button>
        </el-radio-group>
        <base-export-btn
          :table="curTable"
          :isExportBtn="false"
          :isMultiple="true"
        ></base-export-btn>
      </div>
    </div>
    <div v-if="radio == 2" class="echarts">
      <el-checkbox-group
        v-model="nameCheck"
        @change="nameCheckChange"
        class="nameCheck"
        :min="1"
      >
        <el-checkbox
          v-for="name in nameList"
          :key="name"
          :label="name"
          :value="name"
          style="width: 70px"
        ></el-checkbox>
      </el-checkbox-group>
      <div style="display: flex">
        <div
          style="width: 50%; height: 270px"
          id="scoreName"
          class="echartitem"
        ></div>
        <div
          style="width: 50%; height: 270px"
          id="rankName"
          class="echartitem"
        ></div>
      </div>
      <div
        v-if="isPreCompare"
        style="width: auto; height: 250px"
        id="compare"
        class="echartitem"
      ></div>
    </div>
    <base-table
      v-else
      ref="baseTable"
      :table-height="tableHeight"
      :table="curTable"
    ></base-table>
    <sort-dialog
      v-if="sortCompareDialog.show"
      :curTable="curTable"
      @onClose="sortCompareDialog.show = false"
    ></sort-dialog>
    <rank-compare-dialog
      v-if="rankCompareDialog.show"
      :curTable="curTable"
      @onClose="rankCompareDialog.show = false"
    ></rank-compare-dialog>
  </div>
</template>

<script>
import BaseClassSetting from '../base/baseClassSetting'
import BaseTable from '../base/baseTable'
import SortDialog from '../components/sortDialog'
import { mapState, mapGetters } from 'vuex'
import BaseExportBtn from '../base/baseExportBtn'
import RankCompareDialog from '../components/rankCompareDialog'
// import baseMixin from '../base/baseMixin'
export default {
  name: 'ScoreTemplate',
  components: {
    BaseClassSetting,
    BaseTable,
    SortDialog,
    BaseExportBtn,
    RankCompareDialog
  },
  props: {
    tableHeight: {
      type: Number,
      default: 500
    }
  },
  data() {
    return {
      sortCompareDialog: {
        show: false
      },
      rankCompareDialog: {
        show: false
      },
      nameList: [],
      nameListObj: {},
      preNameListObj: {},
      radio: 1,
      nameCheck: [],
      echartsScoreName: null,
      echartsRankName: null,
      echartsCompare: null,
      xAxisData: [],
      xAxisRankData: [],
      contentHeight: 300
    }
  },
  computed: {
    ...mapState({
      tables: state => state.tables,
      subjectMap: state => state.subjectMap,
      isShowMenu: state => state.isShowMenu
    }),
    ...mapGetters(['curTable', 'subjectList', 'subjectRankList']),
    // 
    isPreCompare() {
      return this.curTable.isCompare && this.curTable?.extend?.compareColumn
    }
  },
  watch: {
    'curTable.id': {
      handler() {
        this.dataInitHandle()
        this.typeChangeHandle()
      },
      immediate: true
    },
    isShowMenu() {
      this.echartsResize()
    }
  },
  mounted() {
    this.contentHeight =
      this.tableHeight - this.$el.childNodes[0].offsetHeight + 50
  },
  destroyed() {
    this.destroyedEchart()
  },
  methods: {
    dataInitHandle() {
      this.nameCheck = [this.curTable.data[0]['']]
      let nameListObj = {}
      let nameList = [],
        xAxisData = [],
        xAxisRankData = []
      this.curTable.data.forEach(item => {
        let name = item['']
        nameListObj[name] = item
        nameList.push(name)
      })
      this.nameList = nameList
      this.nameListObj = nameListObj
      // 
      this.curTable.column.forEach(key => {
        this.subjectList.includes(key) && xAxisData.push(key)
      })
      this.curTable.column.forEach(key => {
        this.subjectRankList.includes(key) && xAxisRankData.push(key)
      })
      xAxisRankData.push('')
      this.xAxisData = xAxisData
      this.xAxisRankData = xAxisRankData
      if (this.isPreCompare) {
        this.compareInitHandle()
      }
    },
    // 
    compareInitHandle() {
      ;(this.curTable?.extend?.preTable.data || []).forEach(item => {
        this.preNameListObj[item['']] = item
      })
    },
    async openSortDialog() {
      try {
        await this.$refs.setting.validate()
        this.sortCompareDialog.show = true
      } catch (err) {
        console.log(err)
        this.$message.error('')
      }
    },
    typeChangeHandle() {
      this.radio == 1 ? this.destroyedEchart() : this.echartInit()
    },
    destroyedEchart() {
      ;['echartsScoreName', 'echartsRankName', 'echartsCompare'].forEach(
        key => {
          this[key] && this[key].dispose()
        }
      )
    },
    echartsResize() {
      setTimeout(() => {
        ;['echartsScoreName', 'echartsRankName', 'echartsCompare'].forEach(
          key => {
            this[key] && this[key].resize()
          }
        )
      }, 500)
    },
    echartInit() {
      this.$nextTick(() => {
        this.echartsScoreInit()
        this.echartsRankInit()
        this.echartsCompareInit()
      })
    },
    nameCheckChange(val) {
      this.nameCheck = val.splice(1, 1)
      const series = this.getSeries()
      this.echartsScoreName.setOption({
        series: series.score
      })
      this.echartsRankName.setOption({
        series: series.rank
      })
      if (this.isPreCompare) {
        const { series } = this.getCompaseConfig()
        this.echartsCompare.setOption({
          series: series
        })
      }
    },
    getSeries() {
      let series = {
        score: [],
        rank: []
      }
      this.nameCheck.forEach(name => {
        let item = {
          name: this.curTable.name,
          type: 'line',
          label: {
            show: true
          },
          data: []
        }
        let rankItem = {
          name: this.curTable.name,
          type: 'line',
          label: {
            show: true
          },
          data: []
        }
        let nameObj = this.nameListObj[name]
        this.xAxisData.forEach(key => {
          item.data.push(nameObj[key])
        })
        this.xAxisRankData.forEach(key => {
          rankItem.data.push(nameObj[key])
        })
        if (nameObj['']) {
          rankItem.markLine = {
            data: [
              {
                name: '',
                yAxis: nameObj['']
              }
            ]
          }
        } else if (nameObj['']) {
          rankItem.markLine = {
            data: [
              {
                name: '',
                yAxis: nameObj['']
              }
            ]
          }
        }
        if (rankItem.markLine) {
          rankItem.markLine.label = {
            show: true,
            formatter: '{b}:{c}'
          }
        }
        series.score.push(item)
        series.rank.push(rankItem)
      })
      return series
    },
    echartsScoreInit() {
      let { score: series } = this.getSeries()
      this.echartsScoreName = this.$echarts.init(
        document.getElementById('scoreName')
      )
      const options = {
        title: {
          text: '',
          textStyle: {
            fontSize: '14px',
            lineHeight: 30
          }
        },
        grid: {
          top: 75,
          bottom: 35
        },
        legend: {
          show: true,
          // type: 'scroll',
          left: 'center'
          // selectedMode: 'single',
          // selected: selected
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line'
          }
        },
        xAxis: {
          name: '',
          type: 'category',
          data: this.xAxisData
        },
        yAxis: {
          name: '',
          type: 'value',
          nameTextStyle: {
            padding: [0, 50, 5, 0]
          }
        },
        series: series
      }
      this.echartsScoreName.setOption(options)
    },
    echartsRankInit() {
      let { rank: series } = this.getSeries()
      this.echartsRankName = this.$echarts.init(
        document.getElementById('rankName')
      )
      const options = {
        title: {
          text: '',
          textStyle: {
            fontSize: '14px',
            lineHeight: 30
          }
        },
        grid: {
          top: 75,
          bottom: 35
        },
        legend: {
          show: true,
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line'
          }
        },
        xAxis: {
          name: '',
          type: 'category',
          position: 'top',
          data: this.xAxisRankData
        },
        yAxis: {
          name: '',
          type: 'value',
          inverse: true, //
          nameTextStyle: {
            padding: [0, 50, 5, 0]
          }
        },
        series: series
      }
      this.echartsRankName.setOption(options)
    },
    getCompaseConfig() {
      let preTable = this.curTable.extend.preTable
      let series = [
        {
          name: this.curTable.name,
          type: 'line',
          label: {
            show: true
          },
          data: []
        },
        {
          name: preTable.name,
          type: 'line',
          label: {
            show: true
          },
          data: []
        }
      ]
      let xAxis = this.curTable.extend.compareColumn
      const name = this.nameCheck[0]
      let curItem = this.nameListObj[name]
      let preItem = this.preNameListObj[name]
      xAxis.forEach(key => {
        curItem && series[0].data.push(curItem[key])
        preItem && series[1].data.push(preItem[key])
      })
      return {
        series,
        xAxis
      }
    },
    echartsCompareInit() {
      if (!this.isPreCompare) return
      let { xAxis, series } = this.getCompaseConfig()
      this.echartsCompare = this.$echarts.init(
        document.getElementById('compare')
      )
      const options = {
        title: {
          text: '',
          textStyle: {
            fontSize: '14px',
            lineHeight: 30
          }
        },
        grid: {
          top: 75,
          bottom: 35
        },
        legend: {
          show: true,
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'line'
          }
        },
        xAxis: {
          name: '',
          type: 'category',
          position: 'top',
          data: xAxis
        },
        yAxis: {
          name: '',
          type: 'value',
          inverse: true, //
          nameTextStyle: {
            padding: [0, 50, 5, 0]
          }
        },
        series: series
      }
      this.echartsCompare.setOption(options)
    }
  }
}
</script>

<style lang="scss" scoped>
.scoreTemplate {
  .btns {
    display: flex;
    align-items: center;
    padding: 10px;
    .left {
      flex-wrap: wrap;
      display: flex;
      flex: 1;
    }
    .right {
      margin: 0 auto;
    }
  }
  .nameCheck {
    padding: 10px 20px;
  }
  .echarts {
    .echartitem {
      border: 1px solid #e5e7eb;
      box-shadow: rgb(10 9 9 / 10%) 0px 0px 5px;
      margin: 5px;
      border-radius: 5px;
      background: #fff;
    }
    ::v-deep {
      .el-checkbox__label {
        color: #fff;
      }
      .el-checkbox__input.is-disabled + span.el-checkbox__label {
        color: #409eff;
      }
      .el-checkbox__input.is-disabled.is-checked .el-checkbox__inner {
        background-color: #409eff;
        border-color: #409eff;
      }
    }
  }
}
</style>
